.logInBox {
  align-self: center;
  margin-top: 40px;
  width: 40%;
  background-color: rgba(111, 150, 157, 0.572);
  padding: 5px;
  border-radius: 5px;
}

.logInBox label {
  color: black;
}
.logInBox button {
  background-color: #e9e9e9;
  margin-top: 10px;
  border-radius: 5px;
  width: 100px;
  font-family: "ITC Benguiat";
  font-size: 12pt;
}

.logInBox button:hover {
  background-color: black;
  color: red;
}
.registerLink {
  text-decoration: none;
  color: white;
}

.registerLink:hover {
  color: red;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff;
}
